import {
		mapState,  
    } from 'vuex';
import md5 from '../../kayida/common/md5.js'

export default {
	computed: {
	            ...mapState(['userInfo'])  
	        },
	data() {
		return {
			share:0,
			pic:''
		}
	},
	methods: {
		shareBecome() {
			this.share = 1
		},
		shareCancel() {
			this.share = 0
		},
		shareWeixin() {
			uni.share({
			    provider: "weixin",
			    scene: "WXSceneSession",
			    type: 2,
			    imageUrl: this.pic,
			    success: function (res) {
			        uni.showToast({
			        	title:"分享成功",
						icon:"none"
			        });
			    },
			    fail: function (err) {
			        uni.showToast({
			        	title:"分享失败",
			        	icon:"none"
			        });
			    }
			});
			this.share = 0
		},
		shareFriends() {
			uni.share({
			    provider: "weixin",
			    scene: "WXSenceTimeline",
			    type: 2,
			    imageUrl: this.pic,
			    success: function (res) {
			        uni.showToast({
			        	title:"分享成功",
			        	icon:"none"
			        });
			    },
			    fail: function (err) {
			        uni.showToast({
			        	title:"分享失败",
			        	icon:"none"
			        });
			    }
			});
			this.share = 0
		},
	},
	onLoad() {
		let data = {
			user_id: this.userInfo.user_id,
			token: this.userInfo.token,
			sign: md5(this.userInfo.user_id+this.userInfo.token)
		}
		this.request.httpRequest('api/bind/index',data,'GET').then(res => {
			this.pic = res.data.retval.poster
		})
	}
}
